.NavBar {
	width: 100%;
	overflow: hidden;
}

.nav_item {
	width: auto;
	width: 33%;
	display: inline-block;
	margin: 30px 0 10px 0;
	font-weight: bold;
	color: #cef0ff;
	vertical-align: middle;
	text-shadow: 2px 2px #0f2027, 2.1px 2.1px rgb(140, 140, 255),
		4px 4px #0f2027, 4.1px 4.1px rgb(140, 140, 255);
	font-size: 1.25rem;
	font-style: italic;
	text-align: center;
}

.nav_line {
	width: 90%;
	margin: 0 0 30px 5%;
	border-bottom: 1px solid #cef0ff;
}

.underline {
	border-bottom: 1px solid #cef0ff;
}

@media only screen and (max-width: 600px) {
	.nav_item {
		width: 100%;
		margin: 0px;
		padding: 0 15px 0 0;
		font-size: 1rem;
		text-align: right;
	}

}

.about {
    color: white;
    text-align: left;
    font-family: 'Courier New', Courier, monospace;
    width: 65%;
    margin: 0 0 0 17.5%;
    background-color: rgb(35, 35, 35);
    padding: 20px;
    height: auto;
}

.sub_title {
    display: inline;
    border-bottom: 1px solid white;
}

#github_link {
    color: rgb(129, 160, 255);
    font-style: italic;
}


@media only screen and (max-width: 600px) {
	.about {
		width: 95%;
		margin: 10px 0 10px 2.5%;
	}

}

.RecentTrans {
	color: white;
	height: auto;
	min-height: 200px;
	width: 65%;
	margin: 0 0 2.5% 17.5%;
	background-color: rgb(35, 35, 35);
	padding: 20px;
}

.transaction_div {
	border: 1px solid grey;
	padding: 10px;
	margin: 10px 5px 0 5px;
}

.sub_title {
	display: inline;
	border-bottom: 1px solid white;
}

.transaction_coin,
.transaction_time {
	display: inline-block;
	width: 50%;
	text-align: left;
}
.transaction_value,
.transaction_usd {
	display: inline-block;
	width: 50%;
    text-align: right;
    color: green;
}
.transaction_usd {
    font-size: 1.2rem;
    font-weight: bold;
}

.view_all {
    color: rgb(129, 160, 255);
    font-style: italic;
    font-size: .85rem;
    text-align: right;
    margin: 20px 0 0 0;
}

@media only screen and (max-width: 600px) {
	.RecentTrans {
		width: 95%;
		margin: 10px 0 10px 2.5%;
	}

	.transaction_div {
		padding: 5px;
        margin: 10px 5px 0 5px;
        font-size: .85rem;
	}
}

