@font-face {
	font-family: 'Syncopate-Regular';
	src: url(/static/media/Syncopate-Regular.5f2a6299.ttf) format('truetype');
	font-weight: normal;
	font-style: normal;
}
body {
	font-family: 'Syncopate-Regular';

	background: #0f2027; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
	background: -webkit-gradient(
		linear,
		left top, right top,
		from(#2c5364),
		color-stop(#203a43),
		to(#0f2027)
	);
	background: linear-gradient(
		to right,
		#2c5364,
		#203a43,
		#0f2027
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.grid {
	/* position: relative; */
	height: 100%;
	min-height: 100vh;
	padding: 0 0 200px 0;
	background-color: transparent;
	background-image: -webkit-gradient(
			linear,
			left bottom, left top,
			color-stop(24%, transparent),
			color-stop(25%, rgba(255, 255, 255, 0.05)),
			color-stop(26%, rgba(255, 255, 255, 0.05)),
			color-stop(27%, transparent),
			color-stop(74%, transparent),
			color-stop(75%, rgba(255, 255, 255, 0.05)),
			color-stop(76%, rgba(255, 255, 255, 0.05)),
			color-stop(77%, transparent),
			to(transparent)
		),
		-webkit-gradient(
			linear,
			left top, right top,
			color-stop(24%, transparent),
			color-stop(25%, rgba(255, 255, 255, 0.05)),
			color-stop(26%, rgba(255, 255, 255, 0.05)),
			color-stop(27%, transparent),
			color-stop(74%, transparent),
			color-stop(75%, rgba(255, 255, 255, 0.05)),
			color-stop(76%, rgba(255, 255, 255, 0.05)),
			color-stop(77%, transparent),
			to(transparent)
		);
	background-image: linear-gradient(
			0deg,
			transparent 24%,
			rgba(255, 255, 255, 0.05) 25%,
			rgba(255, 255, 255, 0.05) 26%,
			transparent 27%,
			transparent 74%,
			rgba(255, 255, 255, 0.05) 75%,
			rgba(255, 255, 255, 0.05) 76%,
			transparent 77%,
			transparent
		),
		linear-gradient(
			90deg,
			transparent 24%,
			rgba(255, 255, 255, 0.05) 25%,
			rgba(255, 255, 255, 0.05) 26%,
			transparent 27%,
			transparent 74%,
			rgba(255, 255, 255, 0.05) 75%,
			rgba(255, 255, 255, 0.05) 76%,
			transparent 77%,
			transparent
		);
	background-size: 50px 50px;
}

p {
	font-size: 1rem;
}

a {
	color: inherit;
}
a:hover {
	color: inherit;
	text-decoration: inherit;
}

ul {
	list-style-type: square;
}

::-webkit-scrollbar {
	width: 15px;
}

::-webkit-scrollbar-track {
	background: rgb(120, 120, 120);
}

::-webkit-scrollbar-thumb {
	background: rgb(70, 70, 70);
}

::-webkit-scrollbar-thumb:hover {
	background: rgb(50, 50, 50);
}

